import Vue from 'vue'
import VueRouter from 'vue-router'
// 路由懒加载
// import Login from '../components/Login.vue'
const Login = () => import(/* webpackChunkName:"login_home_welcome" */ '../components/Login.vue')
// import Home from '../components/Home.vue'
const Home = () => import(/* webpackChunkName:"login_home_welcome" */ '../components/Home.vue')
// imprt Welcome from '../components/Welcome.vue'
const Welcome = () => import(/* webpackChunkName:"login_home_welcome" */ '../components/Welcome.vue')
// imprt Users from '../components/user/User.vue'
const Users = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '../components/user/User.vue')
// imprt Rights from '../components/right/Right.vue'
const Rights = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '../components/right/Right.vue')
// imprt Roles from '../components/right/Role.vue'
const Roles = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '../components/right/Role.vue')
// imprt Goodlist from '../components/good/Goodlist.vue'
const Goodlist = () => import(/* webpackChunkName:"List_Sorts" */ '../components/good/Goodlist.vue')
// imprt Goodsorts from '../components/good/Goodsort.vue'
const Goodsorts = () => import(/* webpackChunkName:"List_Sorts" */ '../components/good/Goodsort.vue')
// imprt Sortparams from '../components/good/Sortparam.vue'
const Sortparams = () => import(/* webpackChunkName:"Params_Add" */ '../components/good/Sortparam.vue')
// imprt Addgood from '../components/good/Addgood.vue'
const Addgood = () => import(/* webpackChunkName:"Params_Add" */ '../components/good/Addgood.vue')
// imprt Orders from '../components/order/Order.vue'
const Orders = () => import(/* webpackChunkName:"Oder_Data" */ '../components/order/Order.vue')
// imprt Data from '../components/dataTotal/Data.vue'
const Data = () => import(/* webpackChunkName:"Oder_Data" */ '../components/dataTotal/Data.vue')
Vue.use(VueRouter)
const routes = [{
  path: '/',
  redirect: '/login'
}, {
  path: '/login',
  component:Login
},
{
  path:'/home',
  component:Home,
  redirect:'/welcome',
  children:[
    {
      path:'/welcome',
      component:Welcome
    },
    {
      path:'/users',
      component: Users
    },
    {
      path:'/roles',
      component:Roles
    },
    {
      path:'/rights',
      component: Rights
    },
    {
      path:'/goods',
      component: Goodlist,
    },
    {
      path:'/categories',
      component: Goodsorts
    },
    {
      path:'/params',
      component: Sortparams
    },
    {
      path:'/orders',
      component: Orders
    },
    {
      path:'/reports',
      component: Data
    },
    {
      path:'/goods/add',
      component:Addgood
    }
  ]
}]
const router = new VueRouter({
  routes
})
// 挂载路由守卫
router.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转而来
  // next 是一个函数 表示放行
  if (to.path === '/login') return next()
  // 获取token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})
export default router
